@charset "UTF-8";

$colour-primary: #996699;
$colour-secondary: #003399;
$colour-accent: $colour-primary;
$colour-info: rgb(159,224,244);
$colour-sidebar: #efefef;
$colour-link: #003399;
$cover-slide-backgound: url(../../oira/style/cover-slide-background.svg); 

$button-small-height: 23px;
$button-small-line-height: 23px;

$button-background-colour-disabled: silver;
$button-background-colour-disabled-hover: silver;
$button-text-colour-disabled: gray;
$button-text-colour-disabled-hover: gray;

body {

	.pat-back-button {
		// line-height: 39px;
	}

	.tool-tile {
		.tool-info .tool-title {
			// font-stretch: condensed;
			font-size: 26px;
			margin-bottom: 15px;
		}
	}

	.risk-title {
		font-stretch: condensed;
	}

	.pat-rich {
		h1 {
			font-stretch: condensed;
			margin-bottom: 30px;
			font-size: 2.5em;
		}

		.summary {
			font-weight: 300;
			font-size: 1.3em;
			// line-height: 1.2;
		}
	}

	.pat-message,
	.pat-well {
		h1 {
			font-stretch: condensed;
			margin-bottom: 0;
			font-size: 1.5em;
		}
	}

	.pat-button {
		// height: 34px;
		// line-height: 37px;
	}

	.tag-cloud {
		.tag {
			line-height: 24px;
			height: 22px;
		}
	}

	#screen-intro {
		.variation-dashboard & {
			h1 {
				background-image: url(../../oira/style/oira-logo-colour.svg);
				background-repeat: no-repeat;
				background-size: contain;
				background-position: top left;
				color: transparent;
				width: 170px;
				height: 80px;
				margin-bottom: 40px;
			}
		}
	}

	#content {
		#main-content {
			&.variation-dashboard {
				#content-pane {
					@media screen and (min-width: 768px) {
						// background: rgba(0, 51, 153, 0.14) url(../../oira/style/stars.svg) no-repeat;
						// background-size: 100% auto;
					}
				}
			}
		}
	}

	
}